"use client";

import React, { useState, useEffect } from 'react';
import { useParams } from 'next/navigation';
import Header from '@/components/Header';
import Link from 'next/link';
import Modal from '@/components/Modal';
import PenghargaanForm from '@/components/forms/PenghargaanForm';
import PenelitianForm from '@/components/forms/PenelitianForm';
import PengabdianForm from '@/components/forms/PengabdianForm';
import PublikasiBukuForm from '@/components/forms/PublikasiBukuForm';
import PublikasiJurnalForm from '@/components/forms/PublikasiJurnalForm';
import RisetHKIForm from '@/components/forms/RisetHKIForm';
import RisetNonHKIForm from '@/components/forms/RisetNonHKIForm';
import { auth } from '@/lib/auth';

interface Penghargaan {
  id: number;
  nama: string;
  bentuk: string;
  tahun: string;
  tingkat: string;
}

interface Penelitian {
  id: number;
  semester: string;
  judul: string;
  tahun: string;
}

interface Pengabdian {
  id: number;
  semester: string;
  judul: string;
  tahun: string;
}

interface PublikasiBuku {
  id: number;
  semester: string;
  penerbit: string;
  judul: string;
  isbn: string;
  tahun: string;
}

interface PublikasiJurnal {
  id: number;
  semester: string;
  namaJurnal: string;
  judul: string;
  jenisJurnal: string;
  tahun: string;
  issn: string;
}

interface RisetHKI {
  id: number;
  semester: string;
  judul: string;
  noHKI: string;
  tahun: string;
  jenis: string;
}

interface RisetNonHKI {
  id: number;
  semester: string;
  judul: string;
  jenis: string;
  tahun: string;
  hasil: string;
}

const DosenDetail = () => {
  const params = useParams();
  const [activeTab, setActiveTab] = useState('penghargaan');
  const [searchTerm, setSearchTerm] = useState('');
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalType, setModalType] = useState('');
  const [editingItem, setEditingItem] = useState<any>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 15;

  // State for dosen data fetched from API
  const [dosenData, setDosenData] = useState<any>(null);
  const [dosenLoading, setDosenLoading] = useState(true);
  const [dosenError, setDosenError] = useState<string | null>(null);

  // State for each tab data
  const [penghargaanData, setPenghargaanData] = useState<any[]>([]);
  const [penelitianData, setPenelitianData] = useState<any[]>([]);
  const [pengabdianData, setPengabdianData] = useState<any[]>([]);
  const [publikasiBukuData, setPublikasiBukuData] = useState<any[]>([]);
  const [publikasiJurnalData, setPublikasiJurnalData] = useState<any[]>([]);
  const [risetHKIData, setRisetHKIData] = useState<any[]>([]);
  const [risetNonHKIData, setRisetNonHKIData] = useState<any[]>([]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        setDosenLoading(true);
        setDosenError(null);

        if (!params?.nidn) return;
        const nidn = params.nidn as string;

        // Get auth token from localStorage
        const authToken = localStorage.getItem('authToken');
        const userRole = localStorage.getItem('userRole');

        const headers: Record<string, string> = {
          'Content-Type': 'application/json'
        };

        if (authToken) {
          headers['Authorization'] = `Bearer ${authToken}`;
        }
        if (userRole) {
          headers['x-user-role'] = userRole;
        }

        // Fetch dosen main data
        const dosenRes = await fetch(`/api/dosen/${nidn}`, { headers });
        if (!dosenRes.ok) {
          throw new Error(`Failed to fetch dosen data: ${dosenRes.status} ${dosenRes.statusText}`);
        }
        const dosenJson = await dosenRes.json();
        if (dosenJson.success) {
          setDosenData(dosenJson.data);
        } else {
          throw new Error(dosenJson.message || 'Failed to fetch dosen data');
        }

        // Fetch each tab data
        const penghargaanRes = await fetch(`/api/dosen/${nidn}/penghargaan`, { headers });
        const penghargaanJson = await penghargaanRes.json();
        if (penghargaanJson.success) {
          setPenghargaanData(penghargaanJson.data);
        }

        const penelitianRes = await fetch(`/api/dosen/${nidn}/penelitian`, { headers });
        const penelitianJson = await penelitianRes.json();
        if (penelitianJson.success) {
          setPenelitianData(penelitianJson.data);
        }

        const pengabdianRes = await fetch(`/api/dosen/${nidn}/pengabdian`, { headers });
        const pengabdianJson = await pengabdianRes.json();
        if (pengabdianJson.success) {
          setPengabdianData(pengabdianJson.data);
        }

        const publikasiBukuRes = await fetch(`/api/dosen/${nidn}/publikasi-buku`, { headers });
        const publikasiBukuJson = await publikasiBukuRes.json();
        if (publikasiBukuJson.success) {
          setPublikasiBukuData(publikasiBukuJson.data);
        }

        const publikasiJurnalRes = await fetch(`/api/dosen/${nidn}/publikasi-jurnal`, { headers });
        const publikasiJurnalJson = await publikasiJurnalRes.json();
        if (publikasiJurnalJson.success) {
          setPublikasiJurnalData(publikasiJurnalJson.data);
        }

        const risetHKIRes = await fetch(`/api/dosen/${nidn}/riset-hki`, { headers });
        const risetHKIJson = await risetHKIRes.json();
        if (risetHKIJson.success) {
          setRisetHKIData(risetHKIJson.data);
        }

        const risetNonHKIRes = await fetch(`/api/dosen/${nidn}/riset-non-hki`, { headers });
        const risetNonHKIJson = await risetNonHKIRes.json();
        if (risetNonHKIJson.success) {
          setRisetNonHKIData(risetNonHKIJson.data);
        }
      } catch (error) {
        console.error('Error fetching dosen data:', error);
        setDosenError(error instanceof Error ? error.message : 'An error occurred while fetching data');
      } finally {
        setDosenLoading(false);
      }
    };

    fetchData();
  }, [params?.nidn]);

  // Reset currentPage when activeTab changes
  useEffect(() => {
    setCurrentPage(1);
  }, [activeTab]);

  // CRUD functions
  const handleAddItem = (type: string) => {
    setModalType(type);
    setEditingItem(null);
    setIsModalOpen(true);
  };

  const handleEditItem = (type: string, item: any) => {
    setModalType(type);
    setEditingItem(item);
    setIsModalOpen(true);
  };

  const handleDeleteItem = async (type: string, id: number) => {
    if (!confirm('Apakah Anda yakin ingin menghapus item ini?')) return;

    setIsLoading(true);
    try {
      const nidn = params.nidn as string;

      // Get auth token from localStorage
      const authToken = localStorage.getItem('authToken');
      const userRole = localStorage.getItem('userRole');

      const headers: Record<string, string> = {
        'Content-Type': 'application/json'
      };

      if (authToken) {
        headers['Authorization'] = `Bearer ${authToken}`;
      }
      if (userRole) {
        headers['x-user-role'] = userRole;
      }

      const response = await fetch(`/api/dosen/${nidn}/${type}/${id}`, {
        method: 'DELETE',
        headers
      });

      if (response.ok) {
        // Refresh data based on type
        switch (type) {
          case 'penghargaan':
            setPenghargaanData(prev => prev.filter(item => item.id !== id));
            break;
          case 'penelitian':
            setPenelitianData(prev => prev.filter(item => item.id !== id));
            break;
          case 'pengabdian':
            setPengabdianData(prev => prev.filter(item => item.id !== id));
            break;
          case 'publikasi-buku':
            setPublikasiBukuData(prev => prev.filter(item => item.id !== id));
            break;
          case 'publikasi-jurnal':
            setPublikasiJurnalData(prev => prev.filter(item => item.id !== id));
            break;
          case 'riset-hki':
            setRisetHKIData(prev => prev.filter(item => item.id !== id));
            break;
          case 'riset-non-hki':
            setRisetNonHKIData(prev => prev.filter(item => item.id !== id));
            break;
        }
      }
    } catch (error) {
      console.error('Error deleting item:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleFormSubmit = async (type: string, data: any) => {
    setIsLoading(true);
    try {
      const nidn = params.nidn as string;
      const isEdit = !!editingItem;
      const url = isEdit
        ? `/api/dosen/${nidn}/${type}/${editingItem.id}`
        : `/api/dosen/${nidn}/${type}`;

      // Get auth token from localStorage
      const authToken = localStorage.getItem('authToken');
      const userRole = localStorage.getItem('userRole');

      const headers: Record<string, string> = {
        'Content-Type': 'application/json'
      };

      if (authToken) {
        headers['Authorization'] = `Bearer ${authToken}`;
      }
      if (userRole) {
        headers['x-user-role'] = userRole;
      }

      const response = await fetch(url, {
        method: isEdit ? 'PUT' : 'POST',
        headers,
        body: JSON.stringify(data),
      });

      const result = await response.json();

      if (response.ok && result.success) {
        // Update state based on type
        switch (type) {
          case 'penghargaan':
            if (isEdit) {
              setPenghargaanData(prev => prev.map(item =>
                item.id === editingItem.id ? result.data : item
              ));
            } else {
              setPenghargaanData(prev => [...prev, result.data]);
            }
            break;
          case 'penelitian':
            if (isEdit) {
              setPenelitianData(prev => prev.map(item =>
                item.id === editingItem.id ? result.data : item
              ));
            } else {
              setPenelitianData(prev => [...prev, result.data]);
            }
            break;
          case 'pengabdian':
            if (isEdit) {
              setPengabdianData(prev => prev.map(item =>
                item.id === editingItem.id ? result.data : item
              ));
            } else {
              setPengabdianData(prev => [...prev, result.data]);
            }
            break;
          case 'publikasi-buku':
            if (isEdit) {
              setPublikasiBukuData(prev => prev.map(item =>
                item.id === editingItem.id ? result.data : item
              ));
            } else {
              setPublikasiBukuData(prev => [...prev, result.data]);
            }
            break;
          case 'publikasi-jurnal':
            if (isEdit) {
              setPublikasiJurnalData(prev => prev.map(item =>
                item.id === editingItem.id ? result.data : item
              ));
            } else {
              setPublikasiJurnalData(prev => [...prev, result.data]);
            }
            break;
          case 'riset-hki':
            if (isEdit) {
              setRisetHKIData(prev => prev.map(item =>
                item.id === editingItem.id ? result.data : item
              ));
            } else {
              setRisetHKIData(prev => [...prev, result.data]);
            }
            break;
          case 'riset-non-hki':
            if (isEdit) {
              setRisetNonHKIData(prev => prev.map(item =>
                item.id === editingItem.id ? result.data : item
              ));
            } else {
              setRisetNonHKIData(prev => [...prev, result.data]);
            }
            break;
        }

        // Refresh dosen main data to keep detail page up to date
        const dosenRes = await fetch(`/api/dosen/${nidn}`, { headers });
        if (dosenRes.ok) {
          const dosenJson = await dosenRes.json();
          if (dosenJson.success) {
            setDosenData(dosenJson.data);
          }
        }

        setIsModalOpen(false);
        setEditingItem(null);
        alert(`${type} ${isEdit ? 'berhasil diperbarui' : 'berhasil ditambahkan'}`);
      } else {
        alert(result.message || `Gagal ${isEdit ? 'memperbarui' : 'menambahkan'} ${type}`);
      }
    } catch (error) {
      console.error('Error saving item:', error);
      alert(`Terjadi kesalahan saat ${editingItem ? 'memperbarui' : 'menambahkan'} ${type}`);
    } finally {
      setIsLoading(false);
    }
  };

  const renderTable = (data: any[], columns: string[], type: string) => {
    const filteredData = data.filter(item =>
      Object.values(item).some((value: unknown) =>
        String(value).toLowerCase().includes(searchTerm.toLowerCase())
      )
    );

    const totalPages = Math.ceil(filteredData.length / itemsPerPage);
    const startIndex = (currentPage - 1) * itemsPerPage;
    const paginatedData = filteredData.slice(startIndex, startIndex + itemsPerPage);

    return (
      <div>
        <div className="overflow-x-auto">
          <table className="min-w-full bg-white border border-gray-200">
            <thead className="bg-red-50">
              <tr>
                {columns.map((column, index) => (
                  <th key={index} className="px-4 py-2 text-left text-xs font-bold text-red-600 uppercase tracking-wider border-b">
                    {column}
                  </th>
                ))}
                {(auth.getUserRole() === 'admin' || auth.getUserRole() === 'dosen') && (
                  <th className="px-4 py-2 text-left text-xs font-bold text-red-600 uppercase tracking-wider border-b">
                    Aksi
                  </th>
                )}
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-200">
              {paginatedData.map((item, index) => (
                <tr key={item.id || index} className="hover:bg-gray-50">
                  {columns.map((column, cellIndex) => {
                    let value = item[column.toLowerCase().replace(/\s+/g, '')] || item[column] || '';
                    if (column === 'No') {
                      value = startIndex + index + 1;
                    }
                    return (
                      <td key={cellIndex} className="px-4 py-2 text-xs text-gray-900 border-b">
                        {String(value)}
                      </td>
                    );
                  })}
                  {(auth.getUserRole() === 'admin' || auth.getUserRole() === 'dosen') && (
                    <td className="px-4 py-2 text-xs text-gray-900 border-b">
                      <div className="flex space-x-2">
                        <button
                          onClick={() => handleEditItem(type, item)}
                          className="bg-blue-500 text-white px-2 py-1 rounded text-xs hover:bg-blue-600 transition-colors"
                          disabled={isLoading}
                        >
                          Edit
                        </button>
                        <button
                          onClick={() => handleDeleteItem(type, item.id)}
                          className="bg-red-500 text-white px-2 py-1 rounded text-xs hover:bg-red-600 transition-colors"
                          disabled={isLoading}
                        >
                          Hapus
                        </button>
                      </div>
                    </td>
                  )}
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        {totalPages > 1 && (
          <div className="flex items-center justify-between mt-4">
            <button
              onClick={() => setCurrentPage(prev => Math.max(1, prev - 1))}
              disabled={currentPage === 1}
              className="bg-gray-500 text-white px-3 py-1 rounded text-xs hover:bg-gray-600 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
            >
              Prev
            </button>
            <span className="text-xs text-gray-600">
              Page {currentPage} of {totalPages}
            </span>
            <button
              onClick={() => setCurrentPage(prev => Math.min(totalPages, prev + 1))}
              disabled={currentPage === totalPages}
              className="bg-gray-500 text-white px-3 py-1 rounded text-xs hover:bg-gray-600 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
            >
              Next
            </button>
          </div>
        )}
      </div>
    );
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <Header />

      <div className="container mx-auto px-4 py-8">
        {/* Breadcrumb */}
        <nav className="mb-6">
          <ol className="flex items-center space-x-2 text-sm font-roboto">
            <li><Link href="/" className="text-red-600 hover:text-red-700">Beranda</Link></li>
            <li className="text-gray-500">/</li>
            <li><Link href="/dosen" className="text-red-600 hover:text-red-700">Dosen</Link></li>
            <li className="text-gray-500">/</li>
            <li className="text-gray-700">Detail</li>
          </ol>
        </nav>

        {dosenData ? (
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
            {/* Profile Section */}
            <div className="lg:col-span-1">
              <div className="bg-white rounded-lg shadow-md p-6">
                <img
                  src={dosenData.foto}
                  alt={dosenData.nama}
                  className="w-[400px] h-[300px] object-cover rounded-lg mb-4"
                  onError={(e) => { e.currentTarget.src = "https://placehold.co/400x300?text=Image+Not+Available"; }}
                />

                <h1 className="text-xl font-bold text-gray-800 font-roboto mb-4">{dosenData.nama}</h1>

                <div className="space-y-3 text-sm font-roboto">
                  <div>
                    <span className="font-medium text-gray-600">NIDN:</span>
                    <p className="text-gray-800">{dosenData.nidn}</p>
                  </div>

                  <div>
                    <span className="font-medium text-gray-600">Email:</span>
                    <p className="text-gray-800">{dosenData.email}</p>
                  </div>

                  <div>
                    <span className="font-medium text-gray-600">No Kontak:</span>
                    <p className="text-gray-800">{dosenData.kontak}</p>
                  </div>

                  <div>
                    <span className="font-medium text-gray-600">Status Dosen:</span>
                    <p className="text-gray-800">{dosenData.status}</p>
                  </div>

                  <div>
                    <span className="font-medium text-gray-600">Pangkat:</span>
                    <p className="text-gray-800">{dosenData.pangkat}</p>
                  </div>
                </div>

                {/* Pendidikan */}
                <div className="mt-6">
                  <h3 className="font-bold text-gray-800 font-roboto mb-3">Pendidikan</h3>
                  <div className="space-y-2">
                    {dosenData.pendidikan?.map((edu: any, index: number) => (
                      <div key={index} className="text-sm font-roboto">
                        <span className="font-medium text-red-600">{edu.jenjang}</span>
                        <p className="text-gray-700">{edu.institusi} ({edu.tahun})</p>
                      </div>
                    ))}
                  </div>
                </div>

                {/* Media Sosial Akademik */}
                <div className="mt-6">
                  <h3 className="font-bold text-gray-800 font-roboto mb-3">Media Sosial Akademik</h3>
                  <div className="grid grid-cols-2 gap-2">
                    <a href={dosenData.socialMedia?.sinta} className="flex items-center justify-center bg-red-500 text-white p-2 rounded text-xs font-roboto hover:bg-red-600 transition-colors">
                      SINTA
                    </a>
                    <a href={dosenData.socialMedia?.googleScholar} className="flex items-center justify-center bg-red-500 text-white p-2 rounded text-xs font-roboto hover:bg-red-600 transition-colors">
                      Google Scholar
                    </a>
                    <a href={dosenData.socialMedia?.scopus} className="flex items-center justify-center bg-red-500 text-white p-2 rounded text-xs font-roboto hover:bg-red-600 transition-colors">
                      Scopus
                    </a>
                    <a href={dosenData.socialMedia?.researchGate} className="flex items-center justify-center bg-red-500 text-white p-2 rounded text-xs font-roboto hover:bg-red-600 transition-colors">
                      ResearchGate
                    </a>
                  </div>
                </div>

                {/* Kepakaran */}
                <div className="mt-6">
                  <h3 className="font-bold text-gray-800 font-roboto mb-3">Kepakaran</h3>
                  <div className="flex flex-wrap gap-2">
                    {dosenData.kepakaran?.map((skill: any, index: number) => (
                      <span key={index} className="bg-red-100 text-red-800 px-2 py-1 rounded text-xs font-roboto">
                        {skill}
                      </span>
                    ))}
                  </div>
                </div>
              </div>
            </div>

            {/* Content Section */}
            <div className="lg:col-span-2">
              {/* Deskripsi */}
              <div className="bg-white rounded-lg shadow-md p-6 mb-6">
                <h2 className="text-lg font-bold text-red-600 font-roboto mb-3">Deskripsi Dosen</h2>
                <p className="text-gray-700 font-roboto text-sm leading-relaxed">{dosenData.deskripsi}</p>
              </div>

              {/* Riwayat Peneliti Tabs */}
              <div className="bg-white rounded-lg shadow-md p-6">
                <h2 className="text-lg font-bold text-red-600 font-roboto mb-4">Riwayat Peneliti</h2>

                {/* Search Box */}
                <div className="mb-4">
                  <input
                    type="text"
                    placeholder="Cari data..."
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg text-sm font-roboto focus:outline-none focus:ring-2 focus:ring-red-500"
                  />
                </div>

                {/* Add Data Button - only visible if admin or dosen */}
                {(auth.getUserRole() === 'admin' || auth.getUserRole() === 'dosen') && (
                  <div className="mb-4">
                    <button
                      onClick={() => handleAddItem(activeTab)}
                      className="bg-green-600 text-white px-4 py-2 rounded hover:bg-green-700 transition"
                    >
                      Tambah Data
                    </button>
                  </div>
                )}

                {/* Tabs */}
                <div className="border-b border-gray-200 mb-4">
                  <nav className="flex space-x-8">
                    {[
                      { id: 'penghargaan', label: 'Penghargaan' },
                      { id: 'penelitian', label: 'Penelitian' },
                      { id: 'pengabdian', label: 'Pengabdian' },
                      { id: 'publikasi-buku', label: 'Publikasi Buku' },
                      { id: 'publikasi-jurnal', label: 'Publikasi Jurnal' },
                      { id: 'riset-hki', label: 'Riset HKI' },
                      { id: 'riset-non-hki', label: 'Riset Non HKI' }
                    ].map((tab) => (
                      <button
                        key={tab.id}
                        onClick={() => setActiveTab(tab.id)}
                        className={`py-2 px-1 border-b-2 font-medium text-xs font-roboto ${
                          activeTab === tab.id
                            ? 'border-red-500 text-red-600'
                            : 'border-transparent text-gray-500 hover:text-gray-700'
                        }`}
                      >
                        {tab.label}
                      </button>
                    ))}
                  </nav>
                </div>

                {/* Tab Content */}
                <div className="mt-4">
                  {activeTab === 'penghargaan' && renderTable(penghargaanData, ['No', 'nama', 'bentuk', 'tahun', 'tingkat'], 'penghargaan')}
                  {activeTab === 'penelitian' && renderTable(penelitianData, ['No', 'semester', 'judul', 'tahun'], 'penelitian')}
                  {activeTab === 'pengabdian' && renderTable(pengabdianData, ['No', 'semester', 'judul', 'tahun'], 'pengabdian')}
                  {activeTab === 'publikasi-buku' && renderTable(publikasiBukuData, ['No', 'semester', 'penerbit', 'judul', 'isbn', 'tahun'], 'publikasi-buku')}
                  {activeTab === 'publikasi-jurnal' && renderTable(publikasiJurnalData, ['No', 'semester', 'namaJurnal', 'judul', 'jenisJurnal', 'tahun', 'issn'], 'publikasi-jurnal')}
                  {activeTab === 'riset-hki' && renderTable(risetHKIData, ['No', 'semester', 'judul', 'noHKI', 'tahun', 'jenis'], 'riset-hki')}
                  {activeTab === 'riset-non-hki' && renderTable(risetNonHKIData, ['No', 'semester', 'judul', 'jenis', 'tahun', 'hasil'], 'riset-non-hki')}
                </div>
              </div>
            </div>
          </div>
        ) : (
          <div className="text-center py-8">
            {dosenLoading ? (
              <p className="text-gray-500">Loading dosen data...</p>
            ) : dosenError ? (
              <p className="text-red-500">Error: {dosenError}</p>
            ) : (
              <p className="text-gray-500">Dosen data not available.</p>
            )}
          </div>
        )}
      </div>

      {/* Modal for Forms */}
      <Modal
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        title={editingItem ? `Edit ${modalType}` : `Tambah ${modalType}`}
      >
        {modalType === 'penghargaan' && (
          <PenghargaanForm
            onSubmit={(data) => handleFormSubmit('penghargaan', data)}
            onCancel={() => setIsModalOpen(false)}
            initialData={editingItem}
            isLoading={isLoading}
          />
        )}
        {modalType === 'penelitian' && (
          <PenelitianForm
            onSubmit={(data) => handleFormSubmit('penelitian', data)}
            onCancel={() => setIsModalOpen(false)}
            initialData={editingItem}
            isLoading={isLoading}
          />
        )}
        {modalType === 'pengabdian' && (
          <PengabdianForm
            onSubmit={(data) => handleFormSubmit('pengabdian', data)}
            onCancel={() => setIsModalOpen(false)}
            initialData={editingItem}
            isLoading={isLoading}
          />
        )}
        {modalType === 'publikasi-buku' && (
          <PublikasiBukuForm
            onSubmit={(data) => handleFormSubmit('publikasi-buku', data)}
            onCancel={() => setIsModalOpen(false)}
            initialData={editingItem}
            isLoading={isLoading}
          />
        )}
        {modalType === 'publikasi-jurnal' && (
          <PublikasiJurnalForm
            onSubmit={(data) => handleFormSubmit('publikasi-jurnal', data)}
            onCancel={() => setIsModalOpen(false)}
            initialData={editingItem}
            isLoading={isLoading}
          />
        )}
        {modalType === 'riset-hki' && (
          <RisetHKIForm
            onSubmit={(data) => handleFormSubmit('riset-hki', data)}
            onCancel={() => setIsModalOpen(false)}
            initialData={editingItem}
            isLoading={isLoading}
          />
        )}
        {modalType === 'riset-non-hki' && (
          <RisetNonHKIForm
            onSubmit={(data) => handleFormSubmit('riset-non-hki', data)}
            onCancel={() => setIsModalOpen(false)}
            initialData={editingItem}
            isLoading={isLoading}
          />
        )}
      </Modal>
    </div>
  );
};

export default DosenDetail;
