import React, { useState } from 'react';

interface Penelitian {
  id?: number;
  semester: string;
  judul: string;
  tahun: string;
}

interface PenelitianFormProps {
  initialData?: Penelitian;
  onSubmit: (data: Penelitian) => void;
  onCancel: () => void;
  isLoading?: boolean;
}

const PenelitianForm: React.FC<PenelitianFormProps> = ({
  initialData,
  onSubmit,
  onCancel,
  isLoading = false
}) => {
  const [formData, setFormData] = useState<Penelitian>(
    initialData || {
      semester: '',
      judul: '',
      tahun: ''
    }
  );

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSubmit(formData);
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div>
        <label className="block text-sm font-medium text-gray-700 mb-1">
          Semester/Tahun
        </label>
        <select
          name="semester"
          value={formData.semester}
          onChange={handleChange}
          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-red-500"
          required
        >
          <option value="">Pilih Semester</option>
          <option value="Semester Ganjil/2030">Semester Ganjil/2030</option>
          <option value="Semester Genap/2030">Semester Genap/2030</option>
          <option value="Semester Ganjil/2029">Semester Ganjil/2029</option>
          <option value="Semester Genap/2029">Semester Genap/2029</option>
          <option value="Semester Ganjil/2028">Semester Ganjil/2028</option>
          <option value="Semester Genap/2028">Semester Genap/2028</option>
          <option value="Semester Ganjil/2027">Semester Ganjil/2027</option>
          <option value="Semester Genap/2027">Semester Genap/2027</option>
          <option value="Semester Ganjil/2026">Semester Ganjil/2026</option>
          <option value="Semester Genap/2026">Semester Genap/2026</option>
          <option value="Semester Ganjil/2025">Semester Ganjil/2025</option>
          <option value="Semester Genap/2025">Semester Genap/2025</option>
          <option value="Semester Ganjil/2024">Semester Ganjil/2024</option>
          <option value="Semester Genap/2024">Semester Genap/2024</option>
          <option value="Semester Ganjil/2023">Semester Ganjil/2023</option>
          <option value="Semester Genap/2023">Semester Genap/2023</option>
          <option value="Semester Ganjil/2022">Semester Ganjil/2022</option>
          <option value="Semester Genap/2022">Semester Genap/2022</option>
          <option value="Semester Ganjil/2021">Semester Ganjil/2021</option>
          <option value="Semester Genap/2021">Semester Genap/2021</option>
          <option value="Semester Ganjil/2020">Semester Ganjil/2020</option>
          <option value="Semester Genap/2020">Semester Genap/2020</option>
          <option value="Semester Ganjil/2019">Semester Ganjil/2019</option>
          <option value="Semester Genap/2019">Semester Genap/2019</option>
          <option value="Semester Ganjil/2018">Semester Ganjil/2018</option>
          <option value="Semester Genap/2018">Semester Genap/2018</option>
          <option value="Semester Ganjil/2017">Semester Ganjil/2017</option>
          <option value="Semester Genap/2017">Semester Genap/2017</option>
          <option value="Semester Ganjil/2016">Semester Ganjil/2016</option>
          <option value="Semester Genap/2016">Semester Genap/2016</option>
          <option value="Semester Ganjil/2015">Semester Ganjil/2015</option>
          <option value="Semester Genap/2015">Semester Genap/2015</option>
          <option value="Semester Ganjil/2014">Semester Ganjil/2014</option>
          <option value="Semester Genap/2014">Semester Genap/2014</option>
          <option value="Semester Ganjil/2013">Semester Ganjil/2013</option>
          <option value="Semester Genap/2013">Semester Genap/2013</option>
          <option value="Semester Ganjil/2012">Semester Ganjil/2012</option>
          <option value="Semester Genap/2012">Semester Genap/2012</option>
          <option value="Semester Ganjil/2011">Semester Ganjil/2011</option>
          <option value="Semester Genap/2011">Semester Genap/2011</option>
          <option value="Semester Ganjil/2010">Semester Ganjil/2010</option>
          <option value="Semester Genap/2010">Semester Genap/2010</option>
        </select>
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700 mb-1">
          Judul Penelitian
        </label>
        <input
          type="text"
          name="judul"
          value={formData.judul}
          onChange={handleChange}
          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-red-500"
          required
        />
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700 mb-1">
          Tahun
        </label>
        <input
          type="text"
          name="tahun"
          value={formData.tahun}
          onChange={handleChange}
          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-red-500"
          placeholder="Masukkan tahun"
          required
        />
      </div>

      <div className="flex justify-end space-x-2 pt-4">
        <button
          type="button"
          onClick={onCancel}
          className="px-4 py-2 bg-gray-500 text-white rounded-lg hover:bg-gray-600 transition-colors"
        >
          Batal
        </button>
        <button
          type="submit"
          className="px-4 py-2 bg-red-500 text-white rounded-lg hover:bg-red-600 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
          disabled={isLoading}
        >
          {isLoading ? 'Menyimpan...' : (initialData ? 'Update' : 'Tambah')}
        </button>
      </div>
    </form>
  );
};

export default PenelitianForm;
